<template>
	<div class="my-nav" ref="nav">
		<div class="my-nav-left">
			<div @click="backHome()" class="nav-top">
				<span class="fa fa-home">&nbsp;&nbsp;&nbsp;&nbsp;控制面板</span>
			</div>
			<div>
		    <el-menu class="el-menu-vertical-demo" background-color="#2E363F" text-color="#fff" active-text-color="#ffd04b">
		      <el-submenu v-for="(nav,index) in navs" :index="nav.nav_id" :key="index" class="my-nav-left-box">
		        <template slot="title">
		          <i class="fa" :class="nav.nav_icon">&nbsp;&nbsp;&nbsp;&nbsp;</i><span>{{nav.nav_title}}</span>
		        </template>
		        <el-menu-item-group>
		          <el-menu-item @click="changeRoute(sub.nav_router)" v-for="sub in nav.sub_navs" :index="sub.nav_id" :key="index">
		          		<i class="fa" :class="sub.nav_icon">&nbsp;&nbsp;&nbsp;&nbsp;</i>{{sub.nav_title}}
		          </el-menu-item>
		        </el-menu-item-group>
		      </el-submenu>
		    </el-menu>
			</div>
		</div>
		<div class="my-nav-right">
			<div class="nav-top">
				<span @click="backHome()" class="fa fa-home">&nbsp;&nbsp;&nbsp;&nbsp;Home</span>
			</div>
			<div class="nav-view">
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	//在当前的组件中使用router，需要导入router，并在组件中加载
	import router from '../router'
	export default {
	  name: 'my-nav',
	  router,
	  mounted(){
	  	this.$refs.nav.style.height = document.body.clientHeight-50+"px";
	  	//组件加载后，使用默认的路由
	  	router.push({ name: "Home" });
	  },
	  data(){
	  	return{
	  		navs:[
	  			{	nav_id:"1",
	  				nav_icon:"fa-star",
	  				nav_title:"表单组件",
	  				sub_navs:[
	  					{nav_id:"1-1",nav_icon:"fa-th-list",nav_title:"基本表单",nav_router:"FormA"},
	  					{nav_id:"1-2",nav_icon:"fa-list-alt",nav_title:"高级表单",nav_router:"FormB"}
	  				]
	  			},
	  			{	nav_id:"2",
	  				nav_icon:"fa-bus",
	  				nav_title:"表格组件",
	  				sub_navs:[
	  					{nav_id:"2-1",nav_icon:"fa-table",nav_title:"基本表格",nav_router:"TableA"},
	  					{nav_id:"2-2",nav_icon:"fa-list",nav_title:"复杂表格",nav_router:"TableB"}
	  				]
	  			},
	  			{	nav_id:"3",
	  				nav_icon:"fa-grav",
	  				nav_title:"其他组件",
	  				sub_navs:[
	  					{nav_id:"3-1",nav_icon:"fa-film",nav_title:"轮播图",nav_router:"OtherA"},
	  					{nav_id:"3-2",nav_icon:"fa-cogs",nav_title:"小工具",nav_router:"OtherB"}
	  				]
	  			},	  			
	  			{	nav_id:"4",
	  				nav_icon:"fa-window-close",
	  				nav_title:"错误页面",
	  				sub_navs:[
	  					{nav_id:"4-1",nav_icon:"fa-bug",nav_title:"404错误",nav_router:"ErrorA"},
	  					{nav_id:"4-2",nav_icon:"fa-close",nav_title:"500错误",nav_router:"ErrorB"}
	  				]
	  			}	  			
	  		]
	  	}
	  },
	  methods:{
	  	changeRoute(rName){
	  		router.push({ name: rName })
	  	},
	  	backHome(){
	  		router.push({ name: "Home" })
	  	}
	  }
	}
</script>

<style lang="less" scoped>
	.my-nav{
		text-align: left;
		display: flex;
		flex-flow: row nowrap;
		.nav-top{
			height: 36px;
			line-height: 36px;
			padding-left: 20px;
			cursor: pointer;
		}
	}
	.my-nav-left{
		flex-grow: 0;
		width: 220px;
		height: 100%;
		background-color: #2E363F;
		.nav-top{
			background-color: #27a9e3;
			color: white;

		}
		ul{
			border-right: none;
		}
		.my-nav-left-box{
			border-bottom: 1px solid #1f262d;
		}
	}
	.my-nav-right{
		box-sizing: border-box;
		padding-top: 36px;
		overflow: auto;
		flex-grow: 1;
		height: 100%;
		background-color: #eee;
		.nav-top{
			position: fixed;
			z-index: 1000;
			left: 220px;
			top: 50px;
			width: 100%;
			background-color: white;
			color: #666;
			font-size: 0.9rem;
		}
		.nav-view{
			padding: 20px;
		}
	}
</style>


