<template>
	<div class="form-a">
		<h1>基本表单组件</h1>
		<hr />
		<el-row :gutter="20">
  		<el-col :span="12">
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-gear">&nbsp;&nbsp;表单类型-A</span>
				  </div>
				  <p>请选择你擅长的HTML5技能</p>
			    <el-radio-group v-model="radioA">
				    <el-radio :label="3">Html</el-radio>
				    <el-radio :label="6">Css</el-radio>
				    <el-radio :label="9">JavaScript</el-radio>
				  </el-radio-group>
				  <p>请选择你喜欢的城市</p>
			    <el-radio-group v-model="radioB">
			      <el-radio-button label="上海"></el-radio-button>
			      <el-radio-button label="北京"></el-radio-button>
			      <el-radio-button label="广州"></el-radio-button>
			      <el-radio-button label="深圳"></el-radio-button>
			    </el-radio-group>				  
				  <p>请选择你喜欢的女孩的类型 (最多选3种)</p>
				  <el-checkbox-group v-model="checkedGirls" :min="1" :max="3">
				    <el-checkbox v-for="girl in girls" :label="girl" :key="girl">{{girl}}</el-checkbox>
				  </el-checkbox-group>				  
  				<p>请输入信息</p>
				  <el-input placeholder="请选择日期" suffix-icon="el-icon-date" v-model="inputA">
				  </el-input>
				  <br /><br />
				  <el-input placeholder="请输入内容" prefix-icon="el-icon-search" v-model="inputB">
				  </el-input>				  
				</el-card>
				
 				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-magic">&nbsp;&nbsp;表单类型-C</span>
				  </div>
				  <p>数字是可以调节的</p>
					<el-input-number v-model="numA" :min="1" :max="100" label="描述文字"></el-input-number>
				  <p>这是一个带清除功能的下拉框</p>
				  <el-select v-model="valueA" clearable placeholder="请选择">
				    <el-option v-for="item in optionsA" :key="item.value" :label="item.label" :value="item.value">
				    </el-option>
				  </el-select>
				  <p>这是一个多层级的下拉框</p>
			    <el-cascader :options="optionsB" v-model="selectedOptions">
					</el-cascader>
 				</el-card> 			
  		</el-col>
	  	<el-col :span="12">
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-coffee">&nbsp;&nbsp;表单类型-B</span>
				  </div>
				  <p>默认打开的开关</p>
				  <el-switch v-model="valueB" active-text="按月付费" inactive-text="按年付费"></el-switch>
				  <p>默认关闭的开关</p>
					<el-switch style="display: block" v-model="valueC" active-color="#13ce66" inactive-color="#ff4949" active-text="按月付费" inactive-text="按年付费"></el-switch>
				  <p>无间隔的滚动条</p>
    			<el-slider v-model="valueD" ></el-slider>
				  <p>有间隔的滚动条</p>
    			<el-slider v-model="valueE" :step="10" show-stops></el-slider>
				</el-card>
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-video-camera">&nbsp;&nbsp;表单类型-D</span>
				  </div>
				  <p>带文字提示的点评</p>
				  <el-rate v-model="valueF" show-text></el-rate>
				  <p>不带文字提示的点评</p>
				  <el-rate v-model="valueG"></el-rate>				  
				</el-card>	  		
	  		
	  		
	  	</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
	  name: 'form-a',
	  mounted(){
      this.$notify({
        title: '欢迎使用',
        message: '所有的功能尽情使用吧',
        duration: 2000,
        type: 'success',
        offset: 100
      });  
	  },
	  data(){
	  	return{
	  		radioA:3,
	  		radioB:"上海",
	  		checkedGirls:["温柔","女汉子"],
	  		girls:["温柔","女汉子","性感","萌妹子","萝莉"],
	  		inputA:"",
	  		inputB:"",
	  		numA:20,
				optionsA: [
					{ value: '选项1', label: '黄金糕'}, 
          { value: '选项2', label: '双皮奶'}, 
          { value: '选项3', label: '蚵仔煎'},
          { value: '选项4', label: '龙须面'}
        ],
        valueA: '',
        valueB: true,
        valueC: false,
        valueD: 10,
        valueE: 50,
        valueF: 4,
        valueG: 1,
       	optionsB: [
	       	{ value: 'zhinan', label: '指南',
	          children: [
	          	{ value: 'shejiyuanze',label: '设计原则',
	            	children: [
	            		{value: 'yizhi',label: '一致'}, 
	            		{value: 'fankui',label: '反馈'}, 
	            		{value: 'xiaolv',label: '效率'}
	            	]
	          	},
	          	{ value: 'daohang',label: '导航',
	              children: [
	            		{value: 'cexiangdaohang',label: '侧向导航'}
	            	]
	          	}
	          ]
	        },
	        { value: 'ziyuan',label: '资源',
	          children: [
	          	{value: 'axure',label: 'Axure Components'}, 
	          	{value: 'sketch',label: 'Sketch Templates'}, 
	          	{value: 'jiaohu',label: '组件交互文档'}
	          ]
	        }
        ],
        selectedOptions: []
	  	}
	  },
	  methods:{
	  	
	  }
	}
</script>

<style lang="less" scoped>
	.form-a{
		h1{
			font-weight: 200;
			margin: 0 0 20px;
		}
		hr{
			height: 1px;
			border: none;
			background-color: grey;
		}
		.el-card{
			margin-bottom: 20px;
		}
	}

</style>


