<template>
	<div class="form-b">
		<h1>高级表单组件</h1>
		<hr />
		<el-row :gutter="20">
  		<el-col :span="12">
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-fighter-jet">&nbsp;&nbsp;表单类型-E</span>
				  </div>
					<el-form ref="form" :model="form" label-width="80px">
					  <el-form-item label="活动名称">
					    <el-input v-model="form.name"></el-input>
					  </el-form-item>
					  <el-form-item label="活动区域">
					    <el-select v-model="form.region" placeholder="请选择活动区域">
					      <el-option label="区域一" value="shanghai"></el-option>
					      <el-option label="区域二" value="beijing"></el-option>
					    </el-select>
					  </el-form-item>
					  <el-form-item label="活动时间">
					    <el-col :span="11">
					      <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
					    </el-col>
					    <el-col class="line" :span="2">-</el-col>
					    <el-col :span="11">
					      <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
					    </el-col>
					  </el-form-item>
					  <el-form-item label="即时配送">
					    <el-switch v-model="form.delivery"></el-switch>
					  </el-form-item>
					  <el-form-item label="活动性质">
					    <el-checkbox-group v-model="form.type">
					      <el-checkbox label="餐厅线上活动" name="type"></el-checkbox>
					      <el-checkbox label="地推活动"     name="type"></el-checkbox>
					      <br />
					      <el-checkbox label="线下主题活动" name="type"></el-checkbox>
					      <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
					    </el-checkbox-group>
					  </el-form-item>
					  <el-form-item label="特殊资源">
					    <el-radio-group v-model="form.resource">
					      <el-radio label="线上品牌商赞助"></el-radio>
					      <el-radio label="线下场地免费"></el-radio>
					    </el-radio-group>
					  </el-form-item>
					  <el-form-item label="活动形式">
					    <el-input type="textarea" v-model="form.desc"></el-input>
					  </el-form-item>
					  <el-form-item>
					    <el-button type="primary">立即创建</el-button>
					    <el-button>取消</el-button>
					  </el-form-item>
					</el-form>				  
				</el-card>
 				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-bullhorn">&nbsp;&nbsp;表单类型-F</span>
				  </div>
				  <p>四种尺寸的颜色选择器</p>
				  <el-color-picker v-model="colorA"></el-color-picker>
					<el-color-picker v-model="colorB" size="medium"></el-color-picker>
					<el-color-picker v-model="colorC" size="small"></el-color-picker>
					<el-color-picker v-model="colorD" size="mini"></el-color-picker>
 				</el-card> 			
  		</el-col>
	  	<el-col :span="12">
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-bell">&nbsp;&nbsp;表单类型-G</span>
				  </div>					
				  <el-transfer v-model="valueA" :data="data"></el-transfer>
				</el-card>
				<el-card class="box-card">
				  <div slot="header" class="clearfix">
				    <span class="fa fa-beer">&nbsp;&nbsp;表单类型-H</span>
				  </div>
				  <p>带日期和时间的选择框</p>
    			<el-date-picker v-model="valueB" type="datetime" placeholder="选择日期时间"></el-date-picker>
				  <p>带快捷方式的选择框</p>
    			<el-date-picker v-model="valueC" type="datetime" placeholder="选择日期时间" align="right" :picker-options="pickerOptions"></el-date-picker>				  
				</el-card>	  		
	  	</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
	  name: 'form-b',
	  mounted(){
      this.$notify({
        title: '欢迎使用',
        message: '所有的功能尽情使用吧',
        duration: 2000,
        type: 'success',
        offset: 100
      });  
      this.valueB = new Date();
	  },
	  data(){
	      const generateData = _ => {
	        const data = [];
	        for (let i = 1; i <= 15; i++) {
	          data.push({
	            key: i,
	            label: `备选项 ${ i }`,
	            disabled: i % 4 === 0
	          });
	        }
	        return data;
      	};
	  	return{
	  		data: generateData(),
	  		valueA: [1, 4],
	  		valueB:"",
	  		valueC:"",
	  		colorA:"#666",
	  		colorB:"#1f3",
	  		colorC:"#c8d",
	  		colorD:"#409EFF",
				form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
       	},
        pickerOptions: {
          shortcuts: [
          	{ text: '今天',
            	onClick(picker) {
              	picker.$emit('pick', new Date());
            	}
          	}, 
          	{ text: '昨天',
          		onClick(picker) {
              	const date = new Date();
              	date.setTime(date.getTime() - 3600 * 1000 * 24);
              	picker.$emit('pick', date);
            	}
          	}, 
          	{ text: '一周前',
              onClick(picker) {
              	const date = new Date();
              	date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              	picker.$emit('pick', date);
            	}
          	}
          ]
        }
	  	}
	  },
	  methods:{
	  	
	  }
	}
</script>

<style lang="less" scoped>
	.form-b{
		h1{
			font-weight: 200;
			margin: 0 0 20px;
		}
		hr{
			height: 1px;
			border: none;
			background-color: grey;
		}
	}
	.box-card{
		margin-bottom: 20px;
	}
</style>


