<template>
  <div>
  	<my-header></my-header>
  	<my-nav></my-nav>
  </div>
</template>

<script>
import myHeader from './components/myHeader'
import myNav from './components/myNav'
export default {
  name: 'app',
  components:{
  	myHeader,
  	myNav
  },
  mounted(){
		const loading = this.$loading({
        lock: true,
        text: '网页正在加载，请稍后......',
        background: 'rgb(255, 255, 255)'
    });
    setTimeout(() => {
      loading.close();
    }, 3000);
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
}
</style>



